// 3.  router  `routes` 
// , 
const router = new VueRouter({
  mode: 'history',
  routes //  routes: routes
});

// ajax 
Vue.http.interceptors.push((request, next) => {
  next((response) => {
    let {status, body} = response;
    if (status == 200) {
      //  header 
      //  body  blob 
      if (response.headers.map['Content-Disposition']) {
        let fileNameArray = response.headers.map['Content-Disposition'][0].split('=');
        let fileName = decodeURIComponent(fileNameArray[1]);
        let links = document.createElement("a");
        document.body.appendChild(links);
        links.style = "display: none";
        let url = window.URL.createObjectURL(body);
        links.href = url;
        links.download = fileName;
        links.click();
        window.URL.revokeObjectURL(url);
        document.body.removeChild(links);
      } else if (body.code == -2) {
        response.status = 500;
        response.statusMessage = body.message || '';
        response.statusText = 'Internal Server Error';
        response.ok = false;
        router.push('/daili/login');
      } else if (body.code != 1) {
        response.status = 500;
        response.statusMessage = body.message || body.msg || '';
        response.statusText = 'Internal Server Error';
        response.ok = false;
      } else {
        response.data = body.result;
      }
    } else {
      response.statusMessage = '';
    }
    return response;
  })
});

