<template lang="html">
  <div class="content-wrapper">
    <!-- Main content -->
    <section class="content">
      <div class="row">
        <div class="col-xs-12">
          <div class="box">
            <div class="box-header">
              <h3 class="box-title"></h3>
            </div>
            <!-- tebs  -->
            <div class="box-body">
              <el-tabs v-model="activeName2" type="card" @tab-click="handleClick">
                <el-tab-pane label="" name="first">
                  <div class="box-body screen-top">
                    <div class="screen-item">
                      <span class="screen-title"></span>
                      <el-input v-model="markCode" placeholder="" size="small" style="width:240px"></el-input>
                    </div>
                    <div class="screen-item">
                      <span class="screen-title"></span>
                      <el-input v-model="merchantName" placeholder="" size="small" style="width:240px"></el-input>
                    </div>
                    <div class="screen-item">
                      <span class="screen-title"></span>
                      <el-button type="primary" size="small" @click="screen"></el-button>
                    </div>
                  </div>
                  <div class="box-body">
                    <el-table :data="tableData" border
                              v-loading="tableLoading"
                              element-loading-text="">
                      <el-table-column prop="markCode" label=""></el-table-column>
                      <el-table-column prop="merchantName" label=""></el-table-column>
                      <el-table-column prop="proxyName" label=""></el-table-column>
                      <el-table-column prop="proxyName1" label=""></el-table-column>
                      <el-table-column label="">
                        <template scope="scope">
                          {{ scope.row.createTime | datetime }}
                        </template>
                      </el-table-column>
                      <el-table-column prop="mobile" label=""></el-table-column>
                      <el-table-column prop="registered" label=""></el-table-column>
                      <el-table-column label="">
                        <template scope="scope">
                          {{ scope.row.authenticationTime | datetime }}
                        </template>
                      </el-table-column>
                      <el-table-column prop="statusValue" label=""></el-table-column>
                    </el-table>
                  </div>
                  <div class="box-body">
                    <el-pagination style="float:right"
                                   @size-change="handleSizeChange"
                                   @current-change="handleCurrentChange"
                                   :current-page="pageNo"
                                   :page-sizes="[20, 100, 200, 500]"
                                   :page-size="pageSize"
                                   layout="total, sizes, prev, pager, next, jumper"
                                   :total="total">
                    </el-pagination>
                  </div>
                </el-tab-pane>
                <el-tab-pane label="" name="second">
                  <div class="box-body screen-top">
                    <div class="screen-item">
                      <span class="screen-title"></span>
                      <el-input v-model="hsy.globalID" placeholder="" size="small" style="width:240px"></el-input>
                    </div>
                    <div class="screen-item">
                      <span class="screen-title"></span>
                      <el-input v-model="hsy.shortName" placeholder="" size="small" style="width:240px"></el-input>
                    </div>
                    <div class="screen-item">
                      <span class="screen-title"></span>
                      <el-button type="primary" size="small" @click="screen_hsy"></el-button>
                    </div>
                  </div>
                  <div class="box-body">
                    <el-table :data="hsy.tableData" border
                              v-loading="hsy.tableLoading"
                              element-loading-text="">
                      <el-table-column prop="globalID" label=""></el-table-column>
                      <el-table-column prop="shortName" label=""></el-table-column>
                      <el-table-column prop="proxyName" label=""></el-table-column>
                      <el-table-column label="">
                        <template scope="scope">
                          {{ scope.row.createTimes | datetime }}
                        </template>
                      </el-table-column>
                      <el-table-column prop="cellphone" label=""></el-table-column>
                      <el-table-column prop="province" label=""></el-table-column>
                      <el-table-column prop="industry" label=""></el-table-column>
                      <el-table-column prop="statusValue" label=""></el-table-column>
                    </el-table>
                  </div>
                  <div class="box-body">
                    <el-pagination style="float:right"
                                   @size-change="handleSizeChange_hsy"
                                   @current-change="handleCurrentChange_hsy"
                                   :current-page="hsy.pageNo"
                                   :page-sizes="[20, 100, 200, 500]"
                                   :page-size="hsy.pageSize"
                                   layout="total, sizes, prev, pager, next, jumper"
                                   :total="hsy.total">
                    </el-pagination>
                  </div>
                </el-tab-pane>
              </el-tabs>
            </div>
            <!-- /.box-header -->

            <!-- /.box-body -->
          </div>
          <!-- /.box -->
        </div>
        <!-- /.col -->
      </div>
      <!-- /.row -->
    </section>
  </div>
</template>
<script lang="babel">
  export default {
    name: 'app',
    data() {
      return {
        total: 0,
        pageSize: 20,
        pageNo: 1,
        tableData: [],
        tableLoading: false,
        markCode: '',
        merchantName: '',
        hsy: {
          total: 0,
          pageSize: 20,
          pageNo: 1,
          tableData: [],
          tableLoading: false,
          globalID: '',
          shortName: '',
        },
      }
    },
    created() {
      this.getData();
      this.getData_hsy();
    },
    methods: {
      handleClick(tab, event) {
        console.log(tab, event);
      },
      screen: function () {
        this.getData();
      },
      screen_hsy: function () {
        this.getData_hsy();
      },
      getData: function () {
        this.tableLoading = true;
        this.$http.post('/daili/queryMerchant/dealerMerchantList', {
          pageSize: this.pageSize,
          pageNo: this.pageNo,
          markCode: this.markCode,
          merchantName: this.merchantName
        }).then(res => {
          this.tableLoading = false;
          this.total = res.data.count;
          this.tableData = res.data.records;
        }, err => {
          this.tableLoading = false;
          this.$message({
            showClose: true,
            message: err.data.msg,
            type: 'error'
          });
        });
      },
      getData_hsy: function () {
        this.hsy.tableLoading = true;
        this.$http.post('/daili/HsyQueryMerchant/hsyMerchantList', {
          pageSize: this.hsy.pageSize,
          pageNo: this.hsy.pageNo,
          globalID: this.hsy.globalID,
          shortName: this.hsy.shortName
        }).then(res => {
          this.hsy.tableLoading = false;
          this.hsy.total = res.data.count;
          this.hsy.tableData = res.data.records;
        }, err => {
          this.hsy.tableLoading = false;
          this.$message({
            showClose: true,
            message: err.data.msg,
            type: 'error'
          });
        });
      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.getData();
      },
      handleSizeChange_hsy(val) {
        this.hsy.pageSize = val;
        this.getData_hsy();
      },
      handleCurrentChange(val) {
        this.pageNo = val;
        this.getData();
      },
      handleCurrentChange_hsy(val) {
        this.hsy.pageNo = val;
        this.getData_hsy();
      }
    }
  }
</script>
<style scoped lang="less">
  .screen-top {
    padding-top: 0 !important;
  }

  .screen-item {
    float: left;
    margin-right: 10px;
  }

  .screen-title {
    display: block;
    height: 24px;
    line-height: 24px;
  }
</style>



