<template lang="html">
  <div class="content-wrapper">
    <!-- Main content -->
    <section class="content">
      <div class="row">
        <div class="col-xs-12">
          <div class="box">
            <div class="box-header">
              <h3 class="box-title"></h3>
            </div>
            <!-- /.box-header -->
            <div class="box-body screen-top">
              <div class="screen-item">
                <span class="screen-title"></span>
                <el-input v-model="orderNo" placeholder="" size="small" style="width:240px"></el-input>
              </div>
              <div class="screen-item">
                <span class="screen-title"></span>
                <el-select v-model="businessType" size="small" clearable placeholder="">
                  <el-option v-for="item in item_businessType"
                             :label="item.label"
                             :value="item.value">
                  </el-option>
                </el-select>
              </div>
              <div class="screen-item">
                <span class="screen-title"></span>
                <el-date-picker size="small"
                                v-model="datetime"
                                type="daterange"
                                align="right"
                                @change="datetimeSelect"
                                placeholder=""
                                :picker-options="pickerOptions2">
                </el-date-picker>
              </div>
              <div class="screen-item">
                <span class="screen-title"></span>
                <el-button type="primary" size="small" @click="screen"></el-button>
              </div>
            </div>
            <div class="box-body">
              <el-table :data="tableData" border
                        v-loading="tableLoading"
                        element-loading-text="">
                <el-table-column type="index" label=""></el-table-column>
                <el-table-column prop="splitOrderNo" label=""></el-table-column>
                <el-table-column label="">
                  <template scope="scope">
                    {{ scope.row.businessType | filter_businessType }}
                  </template>
                </el-table-column>
                <el-table-column label="" width="180">
                  <template scope="scope">
                    {{ scope.row.splitCreateTime | datetime }}
                  </template>
                </el-table-column>
                <el-table-column prop="orderNo" label=""></el-table-column>
                <!--<el-table-column prop="splitSettlePeriod" label=""></el-table-column>-->
                <!--<el-table-column label="">-->
                  <!--<template scope="scope">-->
                    <!--{{ scope.row.settleTime | datetime }}-->
                  <!--</template>-->
                <!--</el-table-column>-->
                <el-table-column prop="dealerName" label=""></el-table-column>
                <el-table-column prop="splitAmount" label="" align="right"></el-table-column>
                <el-table-column prop="remark" label=""></el-table-column>
              </el-table>
            </div>
            <div class="box-body">
              <el-pagination style="float:right"
                             @size-change="handleSizeChange"
                             @current-change="handleCurrentChange"
                             :current-page="pageNo"
                             :page-sizes="[20, 100, 200, 500]"
                             :page-size="pageSize"
                             layout="total, sizes, prev, pager, next, jumper"
                             :total="total">
              </el-pagination>
            </div>
            <!-- /.box-body -->
          </div>
          <!-- /.box -->
        </div>
        <!-- /.col -->
      </div>
      <!-- /.row -->
    </section>
  </div>
</template>
<script lang="babel">
  export default {
    name: 'app',
    data() {
      return {
        total: 0,
        pageSize: 20,
        pageNo: 1,
        tableData: [],
        tableLoading: false,
        orderNo: '',
        businessType: '',
        item_businessType: [
          {
            value: 'hssPay',
            label: '-'
          },
          {
            value: 'hssWithdraw',
            label: '-'
          },
          {
            value: 'hssPromote',
            label: '-'
          },
          {
            value: 'hsyPay',
            label: '-'
          }
        ],
        datetime: '',
        beginDate: '',
        endDate: '',
        pickerOptions2: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        }
      }
    },
    created() {
      this.getData();
    },
    methods: {
      datetimeSelect: function (val) {
        let format = val.split(' - ');
        this.beginDate = format[0];
        this.endDate = format[1];
      },
      screen: function () {
        this.getData();
      },
      getData: function () {
        this.tableLoading = true;
        this.$http.post('/daili/profit/details', {
          pageSize: this.pageSize,
          pageNo: this.pageNo,
          orderNo: this.orderNo,
          businessType: this.businessType,
          beginDate: this.beginDate,
          endDate: this.endDate
        }).then(res => {
          this.tableLoading = false;
          this.total = res.data.count;
          this.tableData = res.data.records;
        }, err => {
          this.tableLoading = false;
          this.$message({
            showClose: true,
            message: err.data.msg,
            type: 'error'
          });
        });
      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.getData();
      },
      handleCurrentChange(val) {
        this.pageNo = val;
        this.getData();
      }
    }
  }
</script>
<style scoped lang="less">
  .screen-top {
    padding-top: 0 !important;
  }

  .screen-item {
    float: left;
    margin-right: 10px;
  }

  .screen-title {
    display: block;
    height: 24px;
    line-height: 24px;
  }
</style>



